<template>
  <div id="login-component">
    <el-card
      class="card"
      :body-style="{
        padding: '20px 50px'
      }"
    >
      <div class="title">Login</div>
      <div class="main">
        <div class="item">
          <p>Account</p>
          <el-input v-model="form.username"></el-input>
        </div>
        <div class="item">
          <p>Password</p>
          <el-input type="password" v-model="form.password"></el-input>
        </div>
      </div>
      <div class="footer">
        <el-button type="success" @click="onLoginClick">Login</el-button>
      </div>
    </el-card>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";

import { LoginParams } from "@/apis/auth";
import { Login } from "@/typings/store";

const Component = defineComponent({
  name: "Login",
  setup() {
    const store = useStore();
    const router = useRouter();
    const form = reactive<LoginParams>({
      // username: "",
      // password: "",
      username: "littleking",
      password: "netdbat02777"
    });

    const onLoginClick = async () => {
      const isPass = await store.dispatch(Login, form);
      if (isPass) {
        await router.push("/");
      }
    };

    return {
      form,

      onLoginClick
    };
  }
});

export default Component;
</script>

